@import url('https://fonts.googleapis.com/css2?family=Inknut+Antiqua&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

// flexer
%flexer {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

// modal base style
%modal {
    @extend %flexer;
    justify-content: center;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.9);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 10000;
    transition: all 300ms;
}

// buttons
%btn {
    background: none;
    color: white;
    border-radius: 20px;
    border: 1px solid white;
    transition: background 300ms, color 300ms;
    font-weight: 900;
    cursor: pointer;
    &:hover {
        background-color: #fff;
        color: $primary;
    }
}

// modal content style
%modal-content {
    position: relative;
    height: clamp(500px, 50vw, 600px);
    width: clamp(400px, 40vw, 500px);
    background: $gradient;
    border-radius: 20px;
    padding: 10px;
    color: white;
    transition: transform 300ms;
}

// containers
.container {
    width: clamp(300px, 95vw, 1400px);
    margin-inline: auto;
}

// heading and text
.h1 {
    font-size: clamp(1.5rem, 2vw, 3.5rem);
    font-weight: 900;
    font-family: 'Inknut Antiqua', serif;
}

.h2 {
    font-size: clamp(1rem, 2.5vw, 3.5rem);
    font-family: 'Roboto', sans-serif;
}

.p {
    font-size: clamp(0.8rem, 1.3vw, 1.5rem);
    font-family: 'Roboto', sans-serif;
}
